.App-wrapper {
  min-height: 100vh;
  width: 100%;
  position: relative;
}
.section-cmn-100 {
  height: calc(100vh - 65px);
  width: 100%;
}
.section-cmn-100-m {
  height: max-content;
  width: 100%;
}
@media (max-device-width: 900px), (width<900px) {
  .section-cmn-100 {
    height: max-content;
    margin-bottom: 20px;
  }
}
